<template>
	<div id="category-list">
		<public-header title="全部商品"></public-header>
		<div class="content">
			<div class="wrap">
				<template v-for="(item, index) in categorys">
					<div class="category-item padding-15-l bg-default font-14 margin-10-b" v-if="item.child.length > 0">
						<div class="title padding-15-r padding-10-tb van-hairline--bottom flex-lr">
							<router-link tag="div" :to="util.getUrl({path: '/pages/goods/index', query:{cid: item.id}})">
								<p>{{item.title}}</p>
							</router-link>
							<router-link tag="i" :to="util.getUrl({path: '/pages/category/post', query:{id: item.id}})" class="icon icon-edit font-18 c-info"></router-link>
						</div>
						<div class="child-list padding-15-l">
							<div class="child-item padding-15-r padding-10-tb van-hairline--bottom flex-lr" v-for="(childItem, index) in item.child">
								<router-link tag="div" :to="util.getUrl({path: '/pages/goods/index', query:{child_id: childItem.id, cid: item.id}})">
									<p>{{childItem.title}}</p>
									<p class="c-gray font-13 margin-5-t">{{childItem.goods_num ? childItem.goods_num + '个商品' : '暂无商品'}}</p>
								</router-link>
								<router-link tag="i" :to="util.getUrl({path: '/pages/category/post', query:{id: childItem.id}})" class="icon icon-edit font-18 c-info"></router-link>
							</div>
						</div>
					</div>
					<div class="category-item padding-15-l bg-default font-14 margin-10-b" v-else>
						<div class="title padding-15-r padding-10-tb flex-lr">
							<router-link tag="div" :to="util.getUrl({path: '/pages/goods/index', query:{cid: item.id}})">
								<p>{{item.title}}</p>
								<p class="c-gray font-13 margin-5-t">{{item.goods_num ? item.goods_num + '个商品' : '暂无商品'}}</p>
							</router-link>
							<router-link tag="i" :to="util.getUrl({path: '/pages/category/post', query:{id: item.id}})" class="icon icon-edit font-18 c-info"></router-link>
						</div>
					</div>
				</template>
			</div>
			<div class="action-bottom van-hairline--top">
				<router-link tag="div" :to="util.getUrl({path: '/pages/category/post'})" class="action-item van-hairline--left c-info">
					<i class="icon icon-roundadd font-16"></i>
					新建分类
				</router-link>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				categorys: [],
				showPreLoading: true
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					url: 'manage/goods/category/list',
					success: (res) => {
						that.categorys = res.categorys;
					}
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#category-list .content{
		padding-bottom: 50px;
	}
	#category-list .content .wrap{
		overflow-y: scroll;
		overflow-x: hidden;
		width: 100%;
		height: 100%;
	}
	#category-list .content .action-bottom{
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 50px;
		background-color: #fff;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		font-size: 15px;
	}
	#category-list .content .action-bottom .action-item{
		width: 50%;
		text-align: center;
		padding: 7.5px 0;
		flex: 1;
	}
	#category-list .title{
		min-height: 52px;
	}
	#category-list .child-list .child-item:last-child:after{
		border-color: #fff;
	}
</style>


